import axios from "axios";
import qs from "qs";
import { message } from 'antd';

const axiosInstance = axios.create({
  baseURL: "http://127.0.0.1:4000/api",
  timeout: 10000,
  responseType: "json",
  headers: {
    "Content-Type": "application/x-www-form-urlencoded;charset=utf-8"
  }
});

//POST()
axiosInstance.interceptors.request.use(
  config => {
    // 
    if (
      config.method === "post" ||
      config.method === "put" ||
      config.method === "delete"
    ) {
      // 
      config.data = qs.stringify( {
        ...config.data
      })
    }

    // // token , token
    // if (localStorage.token) {
    //   config.headers.Authorization = localStorage.token;
    // }
    return config;
  },
  error => {
    message.error(error);
    return Promise.reject(error.data.error.message);
  }
);

//()
axiosInstance.interceptors.response.use(
  res => {
    //
    if (res.data.code !== 200) {
      return Promise.reject(res.data.data.error);
    }
    return res;
  },
  error => {
    if (error.response.code === 403) {

    }
    if (error.response.code === 500) {

    }
    if (error.response.code === 502) {

    }
    if (error.response.code === 404) {

    }

    message.error(error);
    //  response 
    return Promise.reject( error.data.data.error);
  }
);


export function Get(url, params) {
  return new Promise((resolve, reject) => {
    axiosInstance.get(url, params).then(function (response) {
      resolve(response.data)
    })
    .catch(function (err) {
      reject(err)
    })
  })
}

export function Post(url, params) {
  return new Promise((resolve, reject) => {
    axiosInstance.post(url, params).then(function (response) {
      resolve(response.data)
    })
    .catch(function (err) {
      reject(err)
    })
  })
}



